.site-list {
  text-transform: uppercase;

  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
}

.site-list__item {
  margin-left: -20px;
  margin-right: -20px;

  text-align: center;
  border-bottom: 1px solid $grey2;

  &:first-child {
    border-top: 1px solid $grey2;
  }
}

.site-list__link {
  display: block;
  padding: 23px 0;

  &:hover {
    color: $black-middle;
  }

  &:focus {
    color: $black-light;
  }
}

@media (min-width: $tablet-width) {
  .site-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    width: 100%;

    color: $black2;

    font-size: 18px;
    line-height: 25px;
    font-weight: 400;
  }

  .site-list__item {
    margin: 0;
    border: none;

    &:first-child {
      border: none;
    }

    &--active {
      position: relative;

      &::after {
        content: "";

        position: absolute;
        right: 0;
        bottom: -8px;
        left: 0;

        height: 3px;

        background: $green;
      }
    }
  }

  .site-list__link {
    padding: 0;
  }
}

@media (min-width: $desktop-width) {
  .site-list {
    font-size: 20px;
    line-height: 25px;
    font-weight: 400;
  }

  .site-list__item {
    margin: 0;

    &::after {
      content: "";

      right: 0;
      bottom: -12px;
      left: 0;

      height: 3px;

      background: $green;
    }

    &--active-firstly {
      &::after {
        background: $white;
      }
    }
  }

  .site-list__link {
    color: $white;

    &:hover {
      color: $white-middle;
    }

    &:focus {
      color: $white-light;
    }

    &--primary {
      color: $black;

      &:hover {
        color: $black-middle;
      }

      &:focus {
        color: $black-light;
      }
    }
  }
}
